  padding: 15px;
}

img {
  width: 100%;
  display: block;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.gallery {
  max-width: 1600px;
  flex-wrap: wrap;
  align-content: center;
  gap: 20px;
  margin-bottom: 20px;
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  list-style: none;
  display: flex;
}

.photo-card {
  border-radius: 0 0 4px 4px;
  flex-basis: calc(25% - 15px);
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

.photo-img {
  height: 240px;
  object-fit: cover;
