  position: relative;
}

.search-form > button {
  height: 100%;
  width: 40px;
  cursor: pointer;
  border: none;
  position: absolute;
  top: 0;
  right: 0;
}

.search-form > button:hover {
  box-shadow: 0 1px 5px rgba(0, 0, 0, .12), 0 1px 5px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

.search-img {
  width: 20px;
  height: 20px;
